<template>
  <div class="QRCode" v-if="msg=='andro_wx'">
    <div class="img"><img src="@/assets/logo.png"></div>
    <div class="text">
      <span class="text1">1、请点击右上角</span><br><br><br>
      <span class="text2">2、选择浏览器打开</span>
    </div>
  </div>
</template>

<script>
export default {
  name: 'QRCode',
  data () {
    return {
      msg: this.appSource()
    }
  },
  mounted () {
    window.location = this.downloadApp();
  },
  methods: {
    downloadApp () {
      let isiOS = this.appSource();
      var downloadUrl = "http://api.honpe.com:81/honpeapp/honpe.apk";
      if (isiOS == 'iOS') {
        downloadUrl = "https://itunes.apple.com/app/id1093743730";
      }
      return downloadUrl;
    },
    appSource () {
      let ua = navigator.userAgent;
      let isWX = !!/MicroMessenger/i.test(ua);
      let isiOS = !!ua.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/);
      let isiPhone = ua.indexOf('iPhone') > -1 || ua.indexOf('Mac') > -1;
      if (isiOS || isiPhone) {
        return "iOS";
      } 
      if (isWX) {
        return "andro_wx";
      }
      return "android";
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.img {
  text-align: right;
}
.img img {
  width: 68px;
}
.text {
  font-size: 18px;
  color: #599edf;
  text-align: right;
}
.text1 {
  margin-right: 39px;
}
.text2 {
  margin-right: 20px;
  
}
</style>



